








import {createComponent, ref} from '@vue/composition-api';

interface Prop {
  addTodos: (todo: string) => void;
}

export default createComponent({
  props: {
    addTodos: {
      type: Function,
      required: true,
    },
  },
  setup(props: Prop) {
    const todo = ref('');
    const onClickAdd = () => {
      props.addTodos(todo.value);
      todo.value = '';
    };
    return {
      todo,
      onClickAdd,
    };
  },
});
